.subscribe_pic {
  width: 630px;
  display: flex;
}
.pic_1_subsr img {
  z-index: 0;
  width: 410px;
  height: 376px;
}
.pic_2_subsr {
  width: 282px;
  height: 348px;
  z-index: 1;
  position: relative;
  left: -124px;
  top: 92px;
}
.subscribe_desc {
  display: flex;
  flex-direction: column;
  width: 520px;
  height: 288px;
  gap: 30px;
}
.input_subscribe {
  display: flex;
  align-items: center;
  width: 516px;
  height: 60px;
  justify-content: space-between;
  padding: 16px 24px;
  background-color: #131e3a;
  border-radius: 10px;
}
.input_field {
  background-color: #131e3a;
  border: none;
  color: white;
}
@media (width < 800px) {
  .subscribe_pic {
    width: 400px;
  }
  .pic_1_subsr img {
    width: 310px;
    height: 276px;
  }
  .pic_2_subsr img {
    width: 182px;
    height: 248px;
  }
}
@media (width < 600px) {
  .subscribe_desc {
    width: 350px;
  }
  .input_subscribe {
    width: 300px;
  }
}
@media (width < 450px) {
  .subscribe_desc {
    width: 250px;
  }
  .input_subscribe {
    width: auto;
    padding: 10px;
  }
  .subscribe_pic {
    width: 250px;
    margin: 0;
  }
  .pic_1_subsr img {
    width: 210px;
    height: 176px;
  }
  .pic_2_subsr img {
    width: 132px;
    height: 188px;
  }
}
