.content_footer {
  display: flex;
  justify-content: space-between;
}
.footer_desc {
  margin-bottom: 24px;
}
.footer_block_1 {
  width: 272px;
  text-align: left;
}
.icon_footer img {
  margin-right: 12px;
}
.footer_block_2 {
  display: flex;
  text-align: left;
  gap: 94px;
  flex-wrap: wrap;
}
.explore {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.statictic {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.company {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.resourses {
  display: flex;
  flex-direction: column;
  gap: 8px;
}
.copyright {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #838382;
  padding-top: 48px;
}
.footer_privacy {
  display: flex;
  gap: 28px;
}
@media (width < 1000px) {
  .footer_block_2 {
    justify-content: center;
  }
}
@media (width < 600px) {
  .content_footer {
    flex-direction: column;
    align-items: center;
    gap: 25px;
  }
}
