.App {
  text-align: center;
  width: auto;
  margin: auto;
  box-sizing: border-box;
}

button {
  padding: 12px 40px;
  display: flex;
  height: 48px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 8px;
  background: #d6ef0e;
  border: none;
  color: #fff;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
  cursor: pointer;
}
@media (width < 450px) {
  button {
    height: 46px;
    width: 32px;
  }
}
@media (width < 340px) {
  button {
    height: 44px;
    width: 28px;
  }
}
h3 {
  color: #fff;

  font-family: Inter;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}
a {
  text-decoration: none;
  color: #fffffd;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}
p {
  color: #fffffd;

  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 160%;
}
h1 {
  color: #fffffd;
  font-family: Canela;
  font-size: 64px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
}
@media (width < 340px) {
  h1 {
    font-size: 54px;
  }
}
h2 {
  color: #fffffd;

  font-family: Canela;
  font-size: 40px;
  font-style: normal;
  font-weight: 500;
  line-height: 120%;
}
@media (width < 340px) {
  h2 {
    font-size: 30px;
  }
}
h4 {
  color: #fffffd;

  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}
h6 {
  color: #fffffd;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 120%;
}
.hero-section {
  display: flex;
  width: auto;
  height: 637px;
  justify-content: space-between;
  margin: 92px 111px;
}

.AmazingArtBox-section {
  display: flex;
  width: auto;
  height: auto;
  flex-direction: column;
  margin: 92px 111px;
  gap: 64px;
}
.createBox {
  display: flex;
  padding: 92px 111px;
  width: auto;
  height: auto;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
}
.popularArtists {
  padding: 92px 111px;
}
.subscribe {
  display: flex;
  height: auto;
  text-align: left;
  justify-content: space-between;
  align-items: center;
  padding: 92px 111px;
}
@media (width < 1400px) {
  .hero-section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    height: auto;
  }
  .subscribe {
    flex-direction: column;
    gap: 50px;
  }
}
@media (width < 600px) {
  .hero-section {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    height: auto;
    gap: 100px;
  }
}
@media (width < 450px) {
  a {
    font-size: 12px;
  }
  .hero-section {
    gap: 200px;
  }
  .AmazingArtBox-section {
    gap: 120px;
    margin: 20px;
  }
  .popularArtists {
    padding-top: 10px;
  }
  .subscribe {
    padding: 0;

    gap: 0;
    margin: 0;
  }
}
@media (width < 1300px) {
  .createBox {
    flex-direction: column;
    padding-bottom: 10px;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  padding: 92px 111px;
  gap: 48px;
}
@media (width < 1000px) {
  .footer {
    padding: 10px;
  }
}
@media (width < 600px) {
  .footer {
    margin-top: 150px;
  }
}
