.headpopular {
  display: flex;

  justify-content: space-between;
  align-items: center;
  width: auto;
  margin-bottom: 64px;
}
.header {
  width: 307px;
  text-align: left;
}
.picpopular {
  display: flex;
  width: auto;

  justify-content: center;
  align-items: center;
  gap: 30px;
  box-sizing: border-box;
}

.sub_block_1 {
  display: flex;
  gap: 30px;
  margin-bottom: 30px;
}
.sub_block_2 {
  display: flex;
  gap: 30px;
}
.sub_pic_1 img {
  width: 300px;
  height: 311px;
}
.sub_pic_2 img {
  width: 520px;
  height: 311px;
}
.sub_pic_3 img {
  width: 410px;
  height: 311px;
}
.sub_pic_4 img {
  width: 410px;
  height: 311px;
}
.sub_pic_5 img {
  width: 410px;
  height: 652px;
}

@media (width < 1300px) {
  .picpopular {
    flex-direction: column;
  }
}
@media (width < 800px) {
  .sub_pic_1 img {
    width: 200px;
    height: 231px;
  }
  .sub_pic_2 img {
    width: 420px;
    height: 231px;
  }
  .sub_pic_3 img {
    width: 310px;
    height: 211px;
  }
  .sub_pic_4 img {
    width: 310px;
    height: 211px;
  }
  .sub_pic_5 img {
    width: 310px;
    height: 502px;
  }
}
@media (width < 600px) {
  /* .sub_pic_1 img {
    width: 120px;
    height: 131px;
  } */
  .sub_pic_2 img {
    width: 310px;
    height: 211px;
  }
  /* .sub_pic_3 img {
    width: 200px;
    height: 131px;
  }
  .sub_pic_4 img {
    width: 200px;
    height: 131px;
  }
  .sub_pic_5 img {
    width: 210px;
    height: 402px;
  } */
  .headpopular {
    flex-direction: column;
  }
}
@media (width < 450px) {
  .sub_block_1 {
    flex-direction: column;
  }
  .sub_block_2 {
    flex-direction: column;
  }
}
