.headerCreate {
  width: 426px;
  text-align: left;
}
.header {
  text-align: left;
}
.headerDesc {
  margin: 20px 0 40px 0;
}
.actions {
  display: flex;
  align-items: center;
  width: 303px;
  justify-content: space-between;
}
.createPic {
  width: auto;
  height: 600px;
  margin: auto;
}
.picCreate_1 {
  position: relative;
  top: 155px;
  right: 100px;
  z-index: 1;
}
.picCreate_1 img {
  width: 410px;
  height: 400px;
}
.picCreate_2 {
  position: relative;
  right: -163px;
  top: -404px;
}
.picCreate_2 img {
  width: 410px;
  height: 475px;
}
@media (width <1600px) {
  .picCreate_1 {
    top: 160px;
  }
}
@media (width <1000px) {
  .picCreate_2 {
    right: 10px;
  }
  .picCreate_1 {
    right: 10px;
    top: 305px;
  }
}
@media (width <800px) {
  .picCreate_2 {
    right: 10px;
  }
  .picCreate_1 {
    right: 10px;
    top: 305px;
  }
}
@media (width <450px) {
  .headerCreate {
    padding: 0;
    width: 300px;
  }

  .picCreate_1 img {
    width: 290px;
    height: 280px;
  }
  .picCreate_2 img {
    width: 290px;
    height: 280px;
  }
  .picCreate_1 {
    right: 10px;
    top: 220px;
  }
  .picCreate_2 {
    right: -25px;
    top: -293px;
  }
  .createPic {
    margin: 10px;
  }
}
@media (width < 350px) {
  .headerCreate {
    width: 300px;
  }
  .actions {
    width: 220px;
    justify-content: space-around;
    margin: auto;
  }
  .picCreate_1 img {
    width: 290px;
    height: 280px;
  }
  .picCreate_2 img {
    width: 290px;
    height: 280px;
  }
  .picCreate_1 {
    right: 10px;
    top: 220px;
  }
  .picCreate_2 {
    right: -25px;
    top: -293px;
  }
}
